<!-- src/views/StaffApprovals.vue -->
<template>
 <div class="apv-page">
  <h2 class="apv-title">  </h2>


  <!--  -->
  <div class="priority-card">
    <!--   -->
    <div v-if="loading" class="priority-loading"> ...</div>
  
    <!--   -->
    <table v-else class="priority-table">
   <thead>
    <tr>
     <th></th>
     <th></th>
     <th> </th>
     <th></th>
     <th> </th>
     <th></th>
     <th></th>
     <th></th>
     <th> </th>
    </tr>
   </thead>
   <tbody>
    <tr v-if="!loading && sponsorList.length === 0">
      <td colspan="10" class="apv-empty"> .</td>
    </tr>
    <tr v-for="sponsor in sponsorList" :key="sponsor.program_code">
     <td>{{ dateFormat(sponsor.deposit_date, "yyyy-MM-dd") }}</td>
     <td>{{ sponsor.userID }}</td>
     <td>{{ numberFormat(sponsor.transaction_amount) }}</td>
     <td>{{ sponsor.program_name }}</td>
     <td>{{ sponsor.sponsor_type }}</td>
     <td>{{ sponsor.status }}</td>
     <td>{{ dateFormat(sponsor.start_date, "yyyy-MM-dd") }}</td>
     <td>{{ dateFormat(sponsor.end_date, "yyyy-MM-dd") }}</td>
     <td>{{ numberFormat(sponsor.goal_amount) }}</td>
    </tr>
   </tbody>
  </table>
 </div>
 </div>
</template>

<script setup>
import axios from "axios";
import dateFormat from "@/utils/dateFormat";
import numberFormat from "@/utils/numberFormat";
import { ref, onBeforeMount } from "vue";

let sponsorList = ref([]);
const loading = ref(false); // loading  

const getSponsorList = async () => {
 loading.value = true; //  
 let result = await axios
  .get(`/api/sponsor/mygiving`)
  .catch((err) => console.log(err));

 if (!result || !result.data) {
  console.log("    .");
  sponsorList.value = [];
  loading.value = false; //  
  return;
 }
 const res = result.data.serviceSponsor;
 let list = JSON.parse(JSON.stringify(res));
 sponsorList.value = list;
 console.log(JSON.parse(JSON.stringify(sponsorList.value)));
 loading.value = false; //  
};

onBeforeMount(() => {
 getSponsorList();
});
defineExpose({
 getSponsorList,
});
</script>

<style scoped>
* {
 font-size: 15px;
}

/* ===     === */
.apv-page {
 max-width: 1600px;
 margin: 24px auto;
 padding: 0 16px 40px;
}

.apv-title {
 font-size: 20px;
 font-weight: 600;
 margin-bottom: 16px;
}


/*     */
.priority-card {
  background: #ffffff;
  border-radius: 12px;
  border: 1px solid #e5e7eb;
  box-shadow: 0 1px 4px rgba(15, 23, 42, 0.06);
  padding: 12px 16px; 
  overflow: hidden; 
}

/*    */
.priority-loading {
 font-size: 13px;
 color: #6b7280;
 padding: 8px 4px;
 text-align: center;
}

/*    */
.priority-table {
 width: 100%;
 border-collapse: collapse;
 font-size: 13px;
}

.priority-table thead {
 background: #f5f7fb;
}

.priority-table th,
.priority-table td {
 padding: 9px 10px;
 border-bottom: 1px solid #edf2f7;
 /*    */
 text-align: center;
}

.priority-table th {
 font-weight: 600;
 color: #4a5568;
 white-space: nowrap;
}

.priority-table tbody tr:hover {
 background: #f9fbff;
}


.apv-empty { /*     */
 text-align: center;
 padding: 14px 0;
 color: #6b7280;
}


/*      (  )  */

/*   (td)   */
.priority-table td:nth-child(3), /*   */
.priority-table td:nth-child(9) { /*   */
 text-align: right !important;
}

/*    ( ) */
.priority-table th:nth-child(1),
.priority-table td:nth-child(1),
.priority-table th:nth-child(8),
.priority-table td:nth-child(8),
.priority-table th:nth-child(9),
.priority-table td:nth-child(9) {
 width: 10%; /*    */
}

.priority-table th:nth-child(3),
.priority-table td:nth-child(3),
.priority-table th:nth-child(10),
.priority-table td:nth-child(10) {
 width: 11%; /*    */
}

.priority-table th:nth-child(2),
.priority-table td:nth-child(2) {
 width: 8%; /*    */
}

.priority-table th:nth-child(5),
.priority-table td:nth-child(5),
.priority-table th:nth-child(6),
.priority-table td:nth-child(6) {
 width: 7%; /*  ,    */
}

</style>

