<template>
  <div class="card dashboard-table-card">
    <div class="card-body">
      <div class="d-flex justify-content-between align-items-center mb-3">
        <h6 class="mb-0">{{ title }}</h6>
        <i v-if="icon" class="material-icons text-muted small">{{ icon }}</i>
      </div>

      <!--    -->
      <div v-if="displayedRows.length === 0" class="empty-text flex-fill">
        {{ emptyMessage }}
      </div>

      <!--    -->
      <div v-else class="table-responsive flex-fill">
        <table class="table mb-0 align-middle dashboard-table">
          <thead>
            <tr>
              <th
                v-for="col in columns"
                :key="col.field"
                :class="[
                  'py-2 small text-secondary fw-semibold',
                  alignClass(col.align),
                ]"
              >
                {{ col.label }}
              </th>
            </tr>
          </thead>
          <tbody>
            <tr
              v-for="(row, idx) in displayedRows"
              :key="idx"
              class="clickable-row"
              @click="onRowClick(row, idx)"
            >
              <td
                v-for="col in columns"
                :key="col.field"
                :class="['py-2 small', alignClass(col.align)]"
              >
                {{ row[col.field] }}
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "DashboardTableCard",
  props: {
    title: String,
    icon: String,
    color: {
      type: String,
      default: "primary",
    },
    columns: {
      type: Array,
      default: () => [],
    },
    rows: {
      type: Array,
      default: () => [],
    },
    maxRows: {
      type: Number,
      default: 0, // 0  
    },
    //      (AA0~AA4)
    userRole: {
      type: String,
      default: null,
    },
    //          ( )
    emptyMessageOverride: {
      type: String,
      default: null,
    },
  },
  computed: {
    displayedRows() {
      if (!Array.isArray(this.rows)) return [];
      if (this.maxRows && this.maxRows > 0) {
        return this.rows.slice(0, this.maxRows);
      }
      return this.rows;
    },
    //   / override     
    emptyMessage() {
      // 1) :       
      if (this.emptyMessageOverride) {
        return this.emptyMessageOverride;
      }

      // 2)    
      switch (this.userRole) {
        case "AA0":
        case null:
          return " .";
        case "AA1":
          return "  .";
        case "AA2":
          return "  .";
        case "AA3":
        case "AA4":
          return "  .";
        default:
          return " .";
      }
    },
  },
  methods: {
    alignClass(align) {
      if (align === "right") return "text-end";
      if (align === "center") return "text-center";
      return "text-start";
    },
    onRowClick(row, idx) {
      this.$emit("row-click", { row, index: idx });
    },
  },
};
</script>

<style scoped>
.dashboard-table-card {
  border-radius: 1rem;
  box-shadow: 0 10px 25px rgba(15, 23, 42, 0.06);
  border: none;
  display: flex;
  flex-direction: column;
}

/* card-body   */
.card-body {
  display: flex;
  flex-direction: column;
}

/*      */
.table-responsive {
  flex: 1; /*  ,  (block) */
}

/*       */
.empty-text {
  font-size: 0.875rem;
  color: #9ca3af;
  text-align: center;
  margin-top: 20px;
}

/*   (2 )    */
@media (min-width: 768px) {
  .dashboard-table-card {
    min-height: 260px;
  }
}

/*    */
.dashboard-table {
  width: 100%;
  table-layout: fixed; /*     */
}

/*    +   ...  */
.dashboard-table th,
.dashboard-table td {
  white-space: nowrap;
  text-align: center !important;
  overflow: hidden; /*     */
  text-overflow: ellipsis; /*  ... */
}

/*     */
.clickable-row {
  cursor: pointer;
}

.clickable-row:hover {
  background-color: #f9fafb;
}
</style>
