<template>
 <div class="apv-page">
  <h2 class="apv-title">  </h2>

  <div class="apv-toolbar apv-toolbar-top">
   <button
    class="apv-btn apv-btn-primary"
    @click="programAdd()"
    v-if="userRole !== 'AA1' && userRole !== 'AA0'"
   >
       
   </button>
   <div class="search-box">
    <input
     type="text"
     placeholder=" "
     v-model="searchKeyword"
    />
   </div>
  </div>

      <div class="priority-card">
           <table class="priority-table">
    <thead>
     <tr>
      <th></th>
      <th></th>
      <th></th>
      <th></th>
      <th> </th>
      <th> </th>
     </tr>
    </thead>
    <tbody>
     <tr
      v-for="program in finalList"
      @click="selectProgram(program)"
      :key="program.program_code"
     >
      <td>{{ program.program_name }}</td>
      <td>{{ program.title }}</td>
      <td>{{ program.writer }}</td>
      <td>{{ dateFormat(program.create_date, "yyyy-MM-dd") }}</td>
      <td>{{ numberFormat(program.goal_amount) }}</td>
      <td>{{ numberFormat(program.used_amount) }}</td>
     </tr>
    </tbody>
   </table>

   <div v-if="sponsorList.length === 0" class="apv-empty">
       .
   </div>
  </div>
 </div>
</template>

<script setup>
import axios from "axios";
import dateFormat from "@/utils/dateFormat";
import numberFormat from "@/utils/numberFormat";
import { ref, onBeforeMount, computed } from "vue";
const userJsonString = localStorage.getItem("user");
let userObject = {};
try {
 userObject = userJsonString ? JSON.parse(userJsonString) : {};
} catch (e) {
 console.error(" user  :", e);
 userObject = {};
}

const userRole = userObject.role || "AA0";

const emit = defineEmits(["go-to-add", "select-program"]);

let sponsorList = ref([]); //    
let programList = ref([]); //     
const searchKeyword = ref("");
const getSponsorList = async (params = {}) => {
 let result = await axios
  .get(`/api/sponsor/activity`, { params: params })
  .catch((err) => console.log(err));

 if (!result || !result.data) {
  console.log("    .");
  sponsorList.value = [];
  return;
 }
 const res = result.data.serviceSponsor;

 let list = JSON.parse(JSON.stringify(res));

 sponsorList.value = list;
 // 2.       programList 
 //  (  programList    )
 if (Object.keys(params).length === 0) {
  programList.value = list;
 }
 console.log(JSON.parse(JSON.stringify(sponsorList.value)));
};

onBeforeMount(() => {
 getSponsorList();
});
defineExpose({
 getSponsorList,
});

const programAdd = () => {
 emit("go-to-add"); // 'go-to-add'  
};
// -------------------------------
//  
// -------------------------------
const finalList = computed(() => {
 const kw = searchKeyword.value.trim();
 if (!kw) return sponsorList.value; //    

 //    
 return sponsorList.value.filter((item) => item.program_name.includes(kw));
});
// client/comments/Sponsor/ProgramList.vue
const selectProgram = async (item) => {

  const userRole = userObject.role;

// AA0 AA1     
const hasDetailPermission = computed(() => {
    return userRole !== 'AA0' && userRole !== 'AA1';
});
if (!hasDetailPermission.value) {
        //    (AA0  AA1)     
        console.log(`[ ] ${userRole}     .`);
        return;
    }

 const res = await axios.get(`/api/sponsor/activity/${item.activity_code}`);

 emit("select-program", {
  ...res.data.activity[0],
  history: res.data.history,
 });
};
</script>
<style scoped>
* {
 font-size: 15px;
}

/* ===     === */
.apv-page {
 max-width: 1600px;
 margin: 24px auto;
 padding: 0 16px 40px;
}

.apv-title {
 font-size: 20px;
 font-weight: 600;
 margin-bottom: 16px;
}

/*   */
.apv-toolbar {
 display: flex;
 align-items: center;
 gap: 12px;
 margin-bottom: 16px;
 flex-wrap: wrap;
}

/*     */
.apv-toolbar-top {
 justify-content: space-between;
 margin-bottom: 8px; /*      */
}


/*  */
.search-box {
 display: flex;
 align-items: center;
 border: 1px solid #bbb;
 padding: 5px 10px;
 border-radius: 10px;
 background-color: white;
}

.search-box input {
 border: none;
 outline: none;
 border-radius: 5px;
}

/*   */
.apv-btn {
 display: inline-flex;
 align-items: center;
 justify-content: center;
 padding: 6px 12px;
 border-radius: 8px;
 border: 1px solid #d2d6e0;
 background: #ffffff;
 font-size: 12px;
 cursor: pointer;
 transition: 0.12s ease-in-out;
 white-space: nowrap;
}

.apv-btn:hover {
 filter: brightness(0.98);
}

.apv-btn:disabled {
 opacity: 0.6;
 cursor: not-allowed;
}

/*    */
.apv-btn-primary {
 background: #7ea6f6;
 border-color: #7ea6f6;
 color: #fff;
}

.apv-btn-primary:hover {
 filter: brightness(0.96);
}

/*     (priority-card) */
.priority-card {
  background: #ffffff;
  border-radius: 12px;
  border: 1px solid #e5e7eb;
  box-shadow: 0 1px 4px rgba(15, 23, 42, 0.06);
  padding: 12px 16px; 
  overflow: hidden; 
}


/*    (priority-table) */
.priority-table {
 width: 100%;
 border-collapse: collapse;
 font-size: 13px;
}

.priority-table thead {
 background: #f5f7fb;
}

.priority-table th,
.priority-table td {
 padding: 9px 10px;
 border-bottom: 1px solid #edf2f7;
 /*    */
 text-align: center;
}

.priority-table th {
 font-weight: 600;
 color: #4a5568;
 white-space: nowrap;
}

/*      */
.priority-table tbody tr {
 cursor: pointer;
}
.priority-table tbody tr:hover {
 background: #f9fbff;
}

.apv-empty { /*     */
 text-align: center;
 padding: 14px 0;
 color: #6b7280;
}

/*      (  )  */

/*   (td)  : 5( ), 6( ) */
.priority-table td:nth-child(5), 
.priority-table td:nth-child(6) {
 text-align: right !important;
}

/*    */
.priority-table th:nth-child(5),
.priority-table td:nth-child(5),
.priority-table th:nth-child(6),
.priority-table td:nth-child(6) {
 width: 15%; /*    */
}

.priority-table th:nth-child(4),
.priority-table td:nth-child(4) {
 width: 15%; /*    */
}

.priority-table th:nth-child(3),
.priority-table td:nth-child(3) {
 width: 10%; /*    */
}

/*  (, )     */

/*   (   ) */
@media (max-width: 800px) {
 .apv-toolbar {
  flex-direction: column;
  align-items: stretch;
  gap: 8px;
 }
}
</style>