//       (,)   .
//  @param {number | string} value -   
//  @returns {string} -  

export default function numberFormat(value) {
  //  null, undefined, 0  
  if (value === null || value === undefined || value === 0) {
    return "0";
  }
  //       Number   ,
  //   toLocaleString()   .
  const number = Number(value);

  //   'ko-KR'     .
  return number.toLocaleString("ko-KR");
}
