<template>
  <div>
    <!-- <h5 class="font-weight-bolder mb-4">   </h5> -->

    <!--  -->
    <material-input
      id="name"
      type="text"
      label=""
      v-model="name"
      size="lg"
      class="mb-3"
    />

    <!--  ( + ) -->
    <div class="d-flex align-items-end gap-2 mb-3">
      <material-input
        id="reg1"
        type="text"
        label=" "
        v-model="regFront"
        @input="regFront = sanitizeNumber($event.target.value, 6)"
        size="lg"
        class="flex-grow-1"
      />
      <material-input
        id="reg2"
        type="password"
        label=" "
        v-model="regBack"
        @input="regBack = sanitizeNumber($event.target.value, 7)"
        size="lg"
        class="flex-grow-1"
      />
    </div>

    <!--  -->
    <sms-verification
      v-model:phone="phone"
      v-model:isVerified="isPhoneVerified"
    />

    <!--  -->
    <div class="d-flex align-items-end gap-2 mb-3">
      <material-input
        id="address"
        type="text"
        label=""
        v-model="address"
        size="lg"
        class="flex-grow-1"
      />
      <material-button
        variant="gradient"
        color="success"
        type="button"
        class="address-btn"
        @click="searchAddress"
      >
        
      </material-button>
    </div>

    <!--  (select box) -->
    <div class="input-group input-group-outline my-3">
      <label class="form-label"></label>
      <select v-model="org_name" class="form-control">
        <option disabled value=""> </option>
        <option
          v-for="org in orgList"
          :key="org.org_name"
          :value="org.org_name"
        >
          {{ org.org_name }}
        </option>
      </select>
    </div>

    <!--  -->
    <material-input
      id="department"
      type="text"
      label=""
      v-model="department"
      size="lg"
      class="mb-3"
    />

    <!--  -->
    <div class="input-group input-group-outline my-3">
      <label class="form-label"></label>
      <select v-model="role" class="form-control">
        <option disabled value=""> </option>
        <option value="AA2"> </option>
        <option value="AA3"> </option>
      </select>
    </div>

    <!--  -->
    <div class="d-flex align-items-end gap-2 mb-3">
      <material-input
        id="email"
        type="email"
        label=""
        v-model="email"
        size="lg"
        class="flex-grow-1"
      />
    </div>

    <!--   -->
    <div class="d-flex justify-content-between mt-4">
      <material-button type="button" @click="$emit('back')"
        ></material-button
      >
      <material-button type="button" color="success" @click="submitForm">
         
      </material-button>
    </div>
  </div>
</template>

<script>
import MaterialInput from '@/components/MaterialInput.vue';
import MaterialButton from '@/components/MaterialButton.vue';
import SmsVerification from '../../components/SignUp/SmsVerification.vue';
import { findAllOrg } from '../../api/user';

export default {
  name: 'SignUpOrgForm',
  components: {
    MaterialInput,
    MaterialButton,
    SmsVerification,
  },
  props: { base: Object },
  data() {
    return {
      // name: 'test12',
      // regFront: '111111',
      // regBack: '22222222',
      // phone: '01011113333',
      // isPhoneVerified: true,
      // address: '123',
      // email: '123@123',
      // org_name: '',
      // orgList: [],
      // department: '',
      // role: 'AA2',
      // emailSent: false,
      name: '',
      regFront: '',
      regBack: '',
      phone: '',
      isPhoneVerified: true,
      address: '',
      email: '',
      org_name: '',
      orgList: [],
      department: '',
      role: '',
      emailSent: false,
    };
  },
  methods: {
    searchAddress() {
      new window.daum.Postcode({
        oncomplete: (data) => {
          let addr = data.address;
          if (data.buildingName != '') {
            addr += `(${data.buildingName})`;
          }
          this.address = addr;
        },
      }).open();
    },
    submitForm() {
      if (!this.name || !this.org_name) {
        return alert('  .');
      }

      if (!this.isPhoneVerified) {
        return alert(' .');
      }

      let formattedPhone = '';
      if (this.phone.length === 10) {
        formattedPhone = `${this.phone.slice(0, 3)}-${this.phone.slice(
          3,
          6
        )}-${this.phone.slice(6)}`;
      } else if (this.phone.length === 11) {
        formattedPhone = `${this.phone.slice(0, 3)}-${this.phone.slice(
          3,
          7
        )}-${this.phone.slice(7)}`;
      }

      this.$emit('submit', {
        name: this.name,
        ssn: `${this.regFront}-${this.regBack}`,
        phone: formattedPhone,
        isPhoneVerified: this.isPhoneVerified,
        address: this.address,
        email: this.email,
        org_name: this.org_name,
        department: this.department,
        role: this.role,
      });
    },
    sanitizeNumber(value, maxLength) {
      //  
      let onlyNumber = value.replace(/[^0-9]/g, '');

      //  
      if (onlyNumber.length > maxLength) {
        onlyNumber = onlyNumber.slice(0, maxLength);
      }

      return onlyNumber;
    },
    validatePhone() {
      //  
      if (this.phone.includes('-')) {
        alert('-  .');
        this.phone = this.phone.replace(/-/g, '');
        return;
      }

      // 
      this.phone = this.phone.replace(/[^0-9]/g, '');

      // 11 
      if (this.phone.length > 11) {
        this.phone = this.phone.slice(0, 11);
      }
    },
    async findOrgList() {
      const result = await findAllOrg();
      this.orgList = result;
    },
  },
  mounted() {
    this.findOrgList();
  },
};
</script>

<style scoped>
.address-btn {
  padding: 10px;
  width: 100px;
  height: 43px;
}
</style>
