<template>
 <div class="apv-page">
  <h2 class="apv-title">  </h2>

  <div class="apv-toolbar apv-toolbar-top">
   <button class="apv-btn apv-btn-primary" @click="programAdd()">
      
   </button>
  </div>

  <div class="apv-toolbar">
   <div class="apv-filters">
    <div class="apv-date-range">
     <input
      type="date"
      v-model="startDate"
      class="apv-input apv-input-date"
     />
     <span class="apv-date-separator">~</span>
     <input
      type="date"
      v-model="endDate"
      class="apv-input apv-input-date"
     />
    </div>

    <select
     name="program_select"
     id="program_select"
     v-model="programCode"
     class="apv-select"
    >
     <option value=""> </option>
     <option
      v-for="program in programList"
      :key="program.program_code"
      :value="program.program_code"
     >
      {{ program.program_name }}
     </option>
    </select>

    <select name="" id="" v-model="sponsorType" class="apv-select">
     <option value=""> </option>
     <option value=""></option>
     <option value=""></option>
    </select>

    <select name="" id="" v-model="status" class="apv-select">
     <option value=""> </option>
     <option value=""></option>
     <option value=""></option>
     <option value=""></option>
     <option value=""></option>
    </select>

    <select name="" id="" v-model="approval_status" class="apv-select">
     <option value=""> </option>
     <option value=""></option>
     <option value=""></option>
     <option value=""></option>
     <option value=""></option>
    </select>
   </div>

   <div class="apv-actions-group">
    <button class="apv-btn apv-btn-outline" @click="search()"></button>
    <button class="apv-btn" @click="clear()"> </button>
   </div>
  </div>

  <div class="priority-card">
   <table class="priority-table">
    <thead>
     <tr>
      <th></th>
      <th> </th>
      <th></th>
      <th></th>
      <th></th>
      <th> </th>
      <th> </th>
      <th></th>
     </tr>
    </thead>
    <tbody>
     <tr
      v-for="program in sponsorList"
      @click="selectProgram(program)"
      :key="program.program_code"
      class="priority-row"
     >
      <td>{{ program.program_name }}</td>
      <td>{{ program.sponsor_type }}</td>
      <td>
       <span class="apv-state-pill" :class="statusClass(program.status)">
        {{ program.status }}
       </span>
      </td>
      <td>{{ dateFormat(program.start_date, "yyyy-MM-dd") }}</td>
      <td>{{ dateFormat(program.end_date, "yyyy-MM-dd") }}</td>
      <td class="priority-align-right">{{ numberFormat(program.goal_amount) }}</td>
      <td class="priority-align-right">{{ numberFormat(program.current_amount) }}</td>
      <td>
       <button
        class="apv-btn apv-btn-xs"
        :class="approvalClass(program.approval_status)"
        @click.stop="Approval(program)"
       >
        {{ program.approval_status }}
       </button>
      </td>
     </tr>
    </tbody>
   </table>

   <div v-if="sponsorList.length === 0" class="priority-empty" colspan="9">
       .
   </div>
  </div>
 </div>
</template>

<script setup>
import axios from "axios";
import dateFormat from "@/utils/dateFormat";
import numberFormat from "@/utils/numberFormat";
import { ref, onBeforeMount } from "vue";
const emit = defineEmits(["go-to-add", "selectProgram"]);
let startDate = ref("");
let endDate = ref("");
let programCode = ref(""); //  Select 
let sponsorType = ref(""); //   Select 
// let amount = ref(null); //  Input 
let status = ref(""); //   Select 
let approval_status = ref(""); //   Select 
let sponsorList = ref([]); //    
let programList = ref([]); //     

//   :   
const statusClass = (statusValue) => {
 switch (statusValue) {
  case "":
   return "apv-state-BA1"; // (BA1)   
  case "":
   return "apv-state-BA2"; // (BA2)   
  case "":
   return "apv-state-BA3"; // (BA3)   
  case "":
   return "apv-state-BA3"; // (BA3)   
  default:
   return "";
 }
};

//   :    
const approvalClass = (approvalStatus) => {
 switch (approvalStatus) {
  case "":
  case "":
   return "apv-btn-outline";
  case "":
  case "":
   return "apv-btn-primary";
  case "":
   return "apv-btn-danger";
  default:
   return "";
 }
};
const getSponsorList = async (params = {}) => {
 let result = await axios
  .get(`/api/sponsor`, { params: params })
  .catch((err) => console.log(err));

 // API     (   )
 if (!result || !result.data) {
  console.log("    .");
  sponsorList.value = [];
  return;
 }
 const res = result.data.serviceSponsor;

 // // 1.   
 // const list = JSON.parse(JSON.stringify(res));
 // sponsorList.value = JSON.parse(JSON.stringify(res));
 // console.log(list);
 const userJsonString = localStorage.getItem("user");

 let userId = null;

 const userObject = JSON.parse(userJsonString);

 userId = String(userObject.user_id);

 let list = JSON.parse(JSON.stringify(res));

 // filter  list   ()
 list = list.filter((item) => {
  //    
  if (
   item.approval_status === "" ||
   item.approval_status === ""
  ) {
   return true;
  }

  //       
  return String(item.writer) === userId;
 });

 sponsorList.value = list;
 // 2.       programList 
 //  (  programList    )
 if (Object.keys(params).length === 0) {
  programList.value = list;
 }
 console.log(JSON.parse(JSON.stringify(sponsorList.value)));
};

onBeforeMount(() => {
 getSponsorList();
});
defineExpose({
 getSponsorList,
});
const search = () => {
 const searchParams = {
  startDate: startDate.value,
  endDate: endDate.value,
  programCode: programCode.value,
  sponsorType: sponsorType.value,
  // amount: amount.value,
  status: status.value,
  approval_status: approval_status.value,
 };

 // getSponsorList     
 console.log(searchParams);
 getSponsorList(searchParams);
};

const programAdd = () => {
 emit("go-to-add"); // 'go-to-add'  
};

const clear = () => {
 startDate.value = "";
 endDate.value = "";
 programCode.value = "";
 sponsorType.value = "";
 // amount.value = null;
 status.value = "";
 approval_status.value = "";
 getSponsorList(); //    
};
// client/comments/Sponsor/ProgramList.vue

const selectProgram = async (program) => {
 console.log(" :", program);

 let result;
 try {
  result = await axios.get(`/api/sponsor/${program.program_code}`);
 } catch (err) {
  console.error("  API  :", err);
  alert("     .");
  return;
 }

 // 1.   
 // ProgramList.vue sponsorRows[0] attachments   .
 const programDetail = result.data.serviceSponsor.sponsorRows[0];
 const attachments = result.data.serviceSponsor.attachments; //    

 // 2.      
 if (programDetail) {
  //       .
  const fullDetail = {
   ...programDetail,
   attachments: attachments, //    
  };
  emit("select-program", fullDetail); // 'select-program'     .
 }
};

const Approval = async (program) => {
 if (program.approval_status === "") {
  alert("   .");
  return;
 }
 if (program.approval_status === "") {
  alert("  .");
  return;
 }
 if (!confirm("  ?")) return;

 try {
  const userJson = localStorage.getItem("user");
  const user = JSON.parse(userJson);
  const requesterCode = user.user_code;

  await axios.post(`/api/sponsor/${program.program_code}/request-approval`, {
   requesterCode,
  });

  alert("  .");
  getSponsorList(); //  
 } catch (err) {
  console.error(err);
  alert("    .");
 }
};
</script>

<style scoped lang="scss">
/* SCSS/Sass      . */
* {
 font-size: 15px;
}
/* === ManagerApprovals.vue     === */
.apv-page {
 max-width: 1700px;
 margin: 24px auto;
 padding: 0 16px 40px;
}

.apv-title {
 font-size: 20px;
 font-weight: 600;
 margin-bottom: 16px;
}

/*   */
.apv-toolbar {
 display: flex;
 justify-content: space-between;
 align-items: center;
 gap: 12px;
 margin-bottom: 16px;
 flex-wrap: wrap;
}

/*     */
.apv-toolbar-top {
 justify-content: flex-start;
 margin-bottom: 8px; /*      */
}

.apv-filters {
 display: flex;
 flex-wrap: wrap;
 gap: 8px;
 flex: 1; /*      */
}

/*    */
.apv-date-range {
 display: flex;
 align-items: center;
 gap: 4px;
}

.apv-date-separator {
 color: #6b7280;
}

/* /   */
.apv-actions-group {
 display: flex;
 gap: 8px;
}

.apv-input,
.apv-select {
 /* input[type="date"] select   */
 min-width: 130px; /*   */
 padding: 7px 10px;
 border-radius: 8px;
 border: 1px solid #d7dce5;
 font-size: 13px;
 outline: none;
 background: #fff;
}

/*      */
.apv-input-date {
 min-width: 100px;
}

.apv-input:focus,
.apv-select:focus {
 border-color: #7ea6f6;
 box-shadow: 0 0 0 1px rgba(126, 166, 246, 0.25);
}

/*   */
.apv-btn {
 display: inline-flex;
 align-items: center;
 justify-content: center;
 padding: 6px 12px;
 border-radius: 8px;
 border: 1px solid #d2d6e0;
 background: #ffffff;
 font-size: 12px;
 cursor: pointer;
 transition: 0.12s ease-in-out;
 white-space: nowrap;
}

.apv-btn:hover {
 filter: brightness(0.98);
}

.apv-btn:disabled {
 opacity: 0.6;
 cursor: not-allowed;
}

/*    (    ) */
.apv-btn-xs {
 padding: 4px 8px;
 font-size: 11px;
}

/*    */
.apv-btn-primary {
 background: #7ea6f6;
 border-color: #7ea6f6;
 color: #fff;
}

.apv-btn-primary:hover {
 filter: brightness(0.96);
}

.apv-btn-danger {
 background: #f76c6c;
 border-color: #f76c6c;
 color: #fff;
}

.apv-btn-danger:hover {
 filter: brightness(0.96);
}

.apv-btn-outline {
 /*    */
 background: #ffffff;
 border-color: #7ea6f6;
 color: #315fbf;
}
.apv-btn-outline:hover {
 background: #f9fbff;
}

/* ================================================== */
/* priority-card, priority-table    */
/* ================================================== */

.priority-card {
 background: #ffffff;
 border-radius: 12px;
 border: 1px solid #e5e7eb;
 box-shadow: 0 1px 4px rgba(15, 23, 42, 0.06);
 padding: 12px 16px;
}

.priority-table {
 width: 100%;
 border-collapse: collapse;
 font-size: 13px;
}

.priority-table thead th {
 text-align: center;
 padding: 10px 8px;
 font-weight: 600;
 font-size: 12px;
 color: #6b7280;
 border-bottom: 1px solid #e5e7eb;
 background: #f9fafb;
 white-space: nowrap;
}

.priority-table tbody td {
 padding: 9px 8px;
 border-bottom: 1px solid #f3f4f6;
 color: #374151;
 vertical-align: middle;
 text-align: center; /*    */
}

.priority-row {
 cursor: pointer;
 transition:
  background-color 0.12s ease,
  transform 0.06s ease;
}

.priority-row:hover {
 background: #f3f4ff;
 transform: translateY(-1px);
}

.priority-empty {
 text-align: center;
 padding: 14px 0;
 color: #9ca3af;
}

/*      */
.priority-align-right {
 text-align: right !important;
}

/* ================================================== */
/* priority-card, priority-table    */
/* ================================================== */

/*  Pill */
.apv-state-pill {
 display: inline-flex;
 align-items: center;
 justify-content: center;
 padding: 2px 8px;
 border-radius: 999px;
 font-size: 11px;
 font-weight: 500;
 white-space: nowrap;
 border: 1px solid transparent; /* priority-badge  border  */
}

/*    (priority-badge  ) */
/* :  -> (BA1)  */
.apv-state-BA1 {
 background: #eef2ff; /* priority-badge-request */
 color: #3730a3;
 border-color: #c7d2fe;
}

/* :  -> (BA2)  */
.apv-state-BA2 {
 background: #ecfdf5; /* priority-badge-approve */
 color: #166534;
 border-color: #bbf7d0;
}

/* /:   -> (BA3)  */
.apv-state-BA3 {
 background: #fef2f2; /* priority-badge-reject */
 color: #b91c1c;
 border-color: #fecaca;
}

/*   */
@media (max-width: 800px) {
 .apv-toolbar {
  flex-direction: column;
  align-items: stretch;
  gap: 16px;
 }
 .apv-filters {
  flex-direction: column;
  gap: 12px;
 }
 .apv-actions-group {
  justify-content: stretch;
  gap: 8px;
 }
 .apv-actions-group button {
  flex: 1;
 }
 .apv-input,
 .apv-select,
 .apv-input-date {
  min-width: unset;
  width: 100%;
 }
}
</style>