<template>
  <section class="p-6 max-w-6xl mx-auto pb-0">
    <div class="page-inner">
      <h3 class="fw-bold text-start text-dark mb-4">  </h3>

      <div class="apv-toolbar">
        <div class="apv-filters">
          <select v-model="stateInput" class="apv-select">
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
          </select>

          <input
            v-model.trim="searchChildInput"
            class="apv-input"
            placeholder=""
            @keyup.enter="searchList"
          />

          <input
            v-model.trim="searchManagerInput"
            class="apv-input"
            placeholder=""
            @keyup.enter="searchList"
          />
        </div>
        <button class="apv-btn apv-btn-outline" @click="searchList">
          
        </button>
      </div>

      <div class="table-card">
        <table class="nice-table">
          <thead>
            <tr>
              <th class="th-cell text-center w-14">No</th>
              <th class="th-cell"></th>
              <th class="th-cell"></th>
              <th class="th-cell"></th>
              <th class="th-cell text-center"></th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(item, index) in list" :key="item.id">
              <td class="td-cell text-center">
                {{ (currentPage - 1) * pageSize + index + 1 }}
              </td>
              <td class="td-cell text-left">
                {{ item.childName ? item.childName : item.writerName }}
              </td>
              <td class="td-cell text-left">{{ item.submitAt }}</td>
              <td class="td-cell text-center">{{ item.managerName }}</td>
              <td class="apv-actions-cell">
                {{ item.status }}

                <div class="status-change-btns">
                  <span
                    v-if="item.status === ''"
                    class="apv-disabled-text"
                  >
                    
                  </span>
                  <div v-else>
                    <template v-for="key in allStatusKeys" :key="key">
                      <button
                        v-if="item.status !== key"
                        :class="[
                          'apv-btn',
                          'apv-btn-sm',
                          { 'apv-btn-success': key == '' },
                        ]"
                        @click="changeStatus(item, key)"
                      >
                        {{ key == '' ? '' : key }}
                      </button>
                    </template>
                  </div>
                </div>
              </td>
            </tr>
            <tr v-if="filteredList.length === 0">
              <td colspan="5" class="text-center">
                   .
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
    <div class="pagination-wrap">
      <button
        class="page-btn"
        :disabled="currentPage === 1"
        @click="setPage(currentPage - 1)"
      >
        &lt;
      </button>

      <button
        v-for="page in pageNumbers"
        :key="page"
        :class="['page-btn', { active: page === currentPage }]"
        @click="setPage(page)"
      >
        {{ page }}
      </button>

      <button
        class="page-btn"
        :disabled="currentPage === totalPages"
        @click="setPage(currentPage + 1)"
      >
        &gt;
      </button>
    </div>
  </section>

  <!--  -->
  <!--   -->
  <ManagersModal
    v-if="showManagerModal"
    :managers="managers"
    :pendingItem="pendingItem"
    @close="showManagerModal = false"
    @select="openConfirmModal"
  />

  <!--   -->
  <ConfirmModal
    v-if="showConfirmModal"
    :pendingItem="pendingItem"
    :selectedManager="selectedManager"
    confirmMode="confirm"
    @close="showConfirmModal = false"
    @confirm="finalApproval"
  />

  <!--   -->
  <ConfirmModal
    v-if="showDoneModal"
    :pendingItem="pendingItem"
    confirmMode="done"
    @closeDone="showDoneModal = false"
  />
</template>

<script>
import {
  getPendingList,
  searchManagers,
  changeStatus as changeStatusApi,
} from '../api/pending';
import ManagersModal from './Pending/ManagerSelectModal.vue';
import ConfirmModal from './Pending/ConfirmModal.vue';

export default {
  name: 'PendingApproval',
  data() {
    return {
      // input  ( , computed  X)
      stateInput: '',
      searchChildInput: '',
      searchManagerInput: '',

      //    (fetchList )
      stateActive: '',
      searchChildActive: '',
      searchManagerActive: '',

      // 
      pageSize: 10,
      currentPage: 1,

      listRaw: [],

      statusMap: {
        : { label: '', class: 'apv-state-CA3' },
      },
      allStatusKeys: [''],

      //  
      // ->  
      // ->  
      // -> 
      showManagerModal: false,
      showConfirmModal: false,
      showDoneModal: false,

      confirmMode: 'confirm',

      managers: [],
      selectedManager: '',
      pendingItem: null,
    };
  },
  components: {
    ManagersModal,
    ConfirmModal,
  },
  computed: {
    filteredList() {
      return this.listRaw.filter((item) => {
        //  
        const searchStatus =
          !this.stateActive || item.status === this.stateActive;
        const searchChild =
          !this.searchChildActive ||
          (item.childName || item.writerName || '').includes(
            this.searchChildActive.trim()
          );
        const searchManager =
          !this.searchManagerActive ||
          (item.managerName || '').includes(this.searchManagerActive.trim());

        return searchStatus && searchChild && searchManager;
      });
    },

    //     (  )
    totalPages() {
      return Math.ceil(this.filteredList.length / this.pageSize);
    },

    //      ( )
    list() {
      const start = (this.currentPage - 1) * this.pageSize;
      const end = start + this.pageSize;

      return this.filteredList.slice(start, end);
    },

    //      
    pageNumbers() {
      return Array.from({ length: this.totalPages }, (_, i) => i + 1);
    },
  },
  methods: {
    // 
    async fetchList() {
      // input -> Active -> computed
      this.stateActive = this.stateInput;
      this.searchChildActive = this.searchChildInput;
      this.searchManagerActive = this.searchManagerInput;

      const data = JSON.parse(localStorage.getItem('user'));
      const result = await getPendingList(data.org_code);

      this.listRaw = result.map((item) => ({
        submit_code: item.submit_code,
        childName: item.child_name,
        writerName: item.writer_name,
        submitAt: item.submit_at?.split(' ')[0],
        managerName: item.manager_name,
        status: this.changeCode('load', item.status),
      }));
      //   1 
      this.currentPage = 1;
    },

    // 
    searchList() {
      // input  Active 
      this.stateActive = this.stateInput;
      this.searchChildActive = this.searchChildInput;
      this.searchManagerActive = this.searchManagerInput;

      //    1
      this.currentPage = 1;
    },

    //   <->   
    changeCode(type, code) {
      if (type == 'change') {
        const result = code == '' ? 'CA1' : 'CA3';
        return result;
      }
      const result = code == 'CA1' ? '' : '';
      return result;
    },

    // , 
    async changeStatus(item) {
      // org_code 
      // ->  
      const data = JSON.parse(localStorage.getItem('user'));
      const managers = await searchManagers(data.org_code);
      this.managers = managers;
      this.pendingItem = item;

      this.selectedManager = '';
      this.showManagerModal = true;
    },

    //   
    // ->     
    openConfirmModal(managerName) {
      this.selectedManager = managerName;

      this.showManagerModal = false;
      this.confirmMode = 'confirm';
      this.showConfirmModal = true;
    },

    // 
    // ->     
    async finalApproval() {
      const res = {
        submit_code: this.pendingItem.submit_code,
        status: 'CA3',
        assi_by: this.selectedManager,
      };

      const result = await changeStatusApi(res);

      this.showConfirmModal = false;

      if (!result.ok) {
        alert(' ');
        return;
      }

      this.confirmMode = 'done';
      this.showDoneModal = true;
      this.fetchList();
    },

    setPage(page) {
      if (page >= 1 && page <= this.totalPages) {
        this.currentPage = page;
      }
    },
  },

  mounted() {
    this.fetchList();
  },
};
</script>

<style scoped>
.page-inner {
  max-width: 1600px;
  height: 670px;
  margin: 0 auto;
}
.apv-page {
  font-family: -apple-system, BlinkMacSystemFont, 'Malgun Gothic', ' ',
    'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
  padding-left: 0 !important;
  padding-right: 0 !important;
}

.apv-title {
  display: none !important;
}

.apv-toolbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  padding: 0 10px;
}

.apv-filters {
  display: flex;
  gap: 10px;
  align-items: center;
}

.apv-input,
.apv-select {
  padding: 8px 12px;
  border: 1px solid #dee2e6;
  border-radius: 8px;
  font-size: 14px;
}

.apv-btn,
.apv-btn-outline {
  padding: 8px 15px;
  border-radius: 8px;
  font-size: 14px;
  cursor: pointer;
  border: 1px solid transparent;
  transition: all 0.2s;
}
.apv-btn-outline {
  background: #ffffff;
  border: 1px solid #dcdcdc;
  padding: 8px 15px;
  height: 38px;
  line-height: 20px;
  color: #495057;
  font-weight: 500;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05);
}

.apv-table th {
  background-color: #f8f9fa;
  font-weight: 700;
  color: #344767;
}

/*       */
.apv-actions-cell {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
  padding-top: 10px !important;
  padding-bottom: 10px !important;
}

.apv-state {
  display: inline-block;
  padding: 4px 8px;
  font-size: 12px;
  font-weight: 600;
  border-radius: 6px;
  white-space: nowrap;
}

.status-change-btns {
  display: flex;
  gap: 4px;
}

/*   */
.apv-btn-sm {
  padding: 3px 6px;
  font-size: 12px;
  line-height: 1.5;
}
.apv-btn-info {
  background-color: #17a2b8;
  color: white;
}
.apv-btn-success {
  height: 30px;
  background-color: #28a745;
  color: white;
}
.apv-btn-danger {
  background-color: #dc3545;
  color: white;
}

/*     */
.apv-state-CA1 {
  /*  ( ) */
  background: #f8d7da;
  color: #721c24;
  border: 1px solid #f5c6cb;
}

.apv-state-CA1 {
  /*  ( ) */
  background: #d4edda;
  color: #155724;
  border: 1px solid #c3e6cb;
}

.apv-disabled-text {
  font-size: 12px;
  color: #999;
  padding: 3px 6px;
  border: 1px solid #ddd;
  border-radius: 6px;
  background: #f5f5f5;
}

.pagination-wrap {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 5px;
  padding: 0 10px;
}

.page-btn {
  padding: 8px 12px;
  font-size: 14px;
  background-color: #f8f9fa;
  border: 1px solid #dee2e6;
  border-radius: 6px;
  cursor: pointer;
  transition: all 0.2s;
}

.page-btn:hover:not(:disabled) {
  background-color: #e9ecef;
}

.page-btn.active {
  background-color: #5e72e4;
  color: white;
  border-color: #5e72e4;
  font-weight: 700;
}

.page-btn:disabled {
  cursor: not-allowed;
  opacity: 0.6;
}

.table-card {
  border-radius: 0.9rem;
  border: 1px solid #e5e7eb;
  background-color: #ffffff;
  box-shadow: 0 10px 24px rgba(15, 23, 42, 0.06);
  overflow: hidden;
  margin-top: 0.5rem;
}

/* nice-table */
.nice-table {
  width: 100%;
  table-layout: fixed;
  border-collapse: collapse;
}

/* th */
.th-cell {
  padding: 0.7rem 0.9rem;
  text-align: center;
  font-weight: 600;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: #6b7280;
  background-color: #f9fafb;
  border-bottom: 1px solid #e5e7eb;
  white-space: nowrap;
}

/* td */
.td-cell {
  padding: 0.65rem 0.9rem;
  border-bottom: 1px solid #f3f4f6;
  color: #111827;
  vertical-align: middle;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

/*  stripe + hover */
.table-row-item:nth-child(even) {
  background-color: #f9fafb;
}
.table-row-item:hover {
  background-color: #f3f4f6;
  transform: translateY(-1px);
  box-shadow: 0 6px 14px rgba(15, 23, 42, 0.08);
}
</style>
