<template>
 <div class="apv-page">
  <h2 class="apv-title"> </h2>

    <div class="apv-toolbar">
   <div class="search-box">
    <input
     type="text"
     placeholder=" "
     v-model="searchKeyword"
    />
   </div>
  </div>

    <div class="priority-card">
      <div v-if="loading" class="priority-loading"> ...</div>

      <table v-else class="priority-table">
    <thead>
     <tr>
      <th></th>
      <th></th>
      <th> </th>
      <th> </th>
      <th> </th>
      <th> </th>
      <th></th>
     </tr>
    </thead>
    <tbody>
     <tr v-if="!loading && finalList.length === 0">
      <td :colspan="isOrgVisible ? 10 : 9" class="apv-empty">
        .
      </td>
     </tr>
     <tr
      v-for="r in finalList"
      :key="r.program_code"
      @click="goDetail(r.program_code)"
      class="click-row"
     >
      <td>{{ r.org_name }}</td>
      <td>{{ r.program_name }}</td>

      <td>
       {{ dateFormat(r.start_date, "yyyy-MM-dd") }}~
       {{ dateFormat(r.end_date, "yyyy-MM-dd") }}
      </td>
      <td>{{ numberFormat(r.goal_amount) }}</td>
      <td>{{ numberFormat(r.current_amount) }}</td>
      <td>{{ numberFormat(r.useAmount) }}</td>
      <td>{{ numberFormat(r.remainder) }}</td>
     </tr>
    </tbody>
   </table>
  </div>
 </div>
</template>

<script setup>
import axios from "axios";
import dateFormat from "@/utils/dateFormat";
import numberFormat from "@/utils/numberFormat";
import { ref, onBeforeMount, computed } from "vue";
import { useRouter } from "vue-router";
const router = useRouter();
let sponsorList = ref([]); //    

// ** [] loading ref  isOrgVisible   **
const searchKeyword = ref("");
const loading = ref(false); 
const isOrgVisible = computed(() => true); 
// ** ------------------------------------------- **

const getSponsorList = async () => {
 loading.value = true; //  
 let result = await axios
  .get(`/api/sponsor/summaryStatementList`)
  .catch((err) => console.log(err));

 // API     (   )
 if (!result || !result.data) {
  console.log("    .");
  sponsorList.value = [];
  loading.value = false; //  
  return;
 }
 const res = result.data.serviceSponsor;

 let list = JSON.parse(JSON.stringify(res));
 sponsorList.value = list;
 console.log(JSON.parse(JSON.stringify(sponsorList.value)));
 loading.value = false; //  
};

onBeforeMount(() => {
 getSponsorList();
});
defineExpose({
 getSponsorList,
});

// -------------------------------
//  
// -------------------------------
const finalList = computed(() => {
 const kw = searchKeyword.value.trim();
 if (!kw) return sponsorList.value; //    

 //    
 return sponsorList.value.filter((item) => item.program_name.includes(kw));
});
// client/comments/Sponsor/ProgramList.vue

const goDetail = (code) => {
  const userDataString = localStorage.getItem("user");
const userData = userDataString ? JSON.parse(userDataString) : {};
const userRole = userData.role;

// AA0 AA1     
const hasDetailPermission = computed(() => {
    return userRole !== 'AA0' && userRole !== 'AA1';
});
if (!hasDetailPermission.value) {
        //    (AA0  AA1)     
        console.log(`[ ] ${userRole}     .`);
        return;
    }


 router.push(`/budget-summary/${code}`);
};
</script>

<style scoped>
/*
 *       .
 * ,  (`priority-card`, `priority-loading`, `priority-table`)
 *    .
 */

* {
 font-size: 15px;
}
/* === ManagerApprovals.vue    === */
.apv-page {
 max-width: 1600px;
 margin: 24px auto;
 padding: 0 16px 40px;
}

.apv-title {
 font-size: 20px;
 font-weight: 600;
 margin-bottom: 16px;
}

/*   */
.apv-toolbar {
 display: flex;
 justify-content: flex-end; /*   */
 align-items: center;
 gap: 12px;
 margin-bottom: 16px;
 flex-wrap: wrap;
}

/*  */
.search-box {
 display: flex;
 align-items: center;
 border: 1px solid #bbb;
 padding: 5px 10px;
 border-radius: 10px;
 background-color: white;
}

.search-box input {
 border: none;
 outline: none;
 border-radius: 5px;
}

/* ---------------------------------------------------- */
/*        ( SponsorshipPlanApproval.vue ) */
/* ---------------------------------------------------- */

.priority-card {
 background: #ffffff;
 border-radius: 12px;
 border: 1px solid #e5e7eb;
 box-shadow: 0 1px 4px rgba(15, 23, 42, 0.06);
 padding: 12px 16px; /* apv-table-wrap   */
}

.priority-loading {
 font-size: 13px;
 color: #6b7280;
 padding: 8px 4px; /* apv-empty    */
}

.priority-table {
 width: 100%;
 border-collapse: collapse;
 font-size: 13px;
 /* apv-table   */
}


/* apv-table  priority-table  */
.priority-table thead {
 background: #f5f7fb;
}

.priority-table th,
.priority-table td {
 padding: 9px 10px;
 border-bottom: 1px solid #edf2f7;
 text-align: center;
}

.priority-table th {
 font-weight: 600;
 color: #4a5568;
 white-space: nowrap;
}

.priority-table tbody tr:hover {
 background: #f9fbff;
}

.apv-empty { /*       */
 text-align: center;
 padding: 14px 0;
 color: #6b7280;
}

.click-row {
 cursor: pointer;
}
.click-row:hover {
 background-color: #eef3ff;
}


/*   /  (  ) */

/*      */
.priority-table td:nth-child(2) {
 text-align: center !important;
}

/*      (4~7 ) */
.priority-table td:nth-child(4),
.priority-table td:nth-child(5),
.priority-table td:nth-child(6),
.priority-table td:nth-child(7) {
 text-align: right !important;
}

/*       */
.priority-table th:nth-child(4),
.priority-table th:nth-child(5),
.priority-table th:nth-child(6),
.priority-table th:nth-child(7) {
 text-align: center !important;
}

/*    */
.priority-table th:nth-child(1),
.priority-table td:nth-child(1) {
 width: 12%;
}

.priority-table th:nth-child(2),
.priority-table td:nth-child(2) {
 width: 13%;
}

.priority-table th:nth-child(3),
.priority-table td:nth-child(3) {
 width: 16%;
}

.priority-table td:nth-child(4),
.priority-table td:nth-child(5),
.priority-table td:nth-child(6),
.priority-table td:nth-child(7) {
 width: 13%;
}
</style>