<template>
  <div class="bg-white">
    <div class="container top-0 position-sticky z-index-sticky">
      <div class="row">
        <div class="col-12">
          <navbar
            isBlur="blur my-3 py-2 mt-4 start-0 end-0 mx-4 shadow blur border-radius-lg"
            btnBackground="bg-gradient-success"
            v-bind:darkMode="true"
          />
        </div>
      </div>
    </div>
    <main class="mt-0 main-content">
      <section>
        <div class="page-header min-vh-100">
          <div class="container">
            <div class="row">
              <div
                class="col-6 d-lg-flex d-none h-100 my-auto pe-0 ps-0 position-absolute top-0 start-0 text-center justify-content-center flex-column"
              >
                <div
                  class="position-relative h-100 m-3 px-7 border-radius-lg d-flex flex-column justify-content-center"
                  :style="{
                    backgroundImage:
                      'url(' +
                      require('@/assets/img/illustrations/illustration-signin.jpg') +
                      ')',
                  }"
                ></div>
              </div>
              <div
                class="col-xl-4 col-lg-5 col-md-7 d-flex flex-column ms-auto me-auto ms-lg-auto me-lg-5"
              >
                <div class="card card-plain">
                  <div class="pb-0 card-header bg-transparent mb-4">
                    <h4 class="font-weight-bolder"></h4>
                    <p class="mb-0">
                          .
                    </p>
                  </div>
                  <div class="card-body">
                    <!-- from -->
                    <form role="form" @submit.prevent v-if="step === 1">
                      <!--  -->
                      <div class="mb-3">
                        <div class="d-flex align-items-end">
                          <!--   -->
                          <div class="flex-grow-1 me-2">
                            <material-input
                              id="user-id"
                              type="text"
                              label=" "
                              size="lg"
                              v-model="userId"
                            />
                          </div>

                          <!--   -->
                          <material-button
                            variant="gradient"
                            color="success"
                            size="lg"
                            class="check-btn"
                            type="button"
                            @click="checkId"
                          >
                            
                          </material-button>
                        </div>
                      </div>
                      <p
                        v-if="idCheck"
                        :style="{
                          color: idCheck == 'true' ? '#2ecc71' : '#e74c3c',
                          fontSize: '0.8rem',
                          marginTop: '-10px',
                          marginLeft: '4px',
                        }"
                      >
                        <span>{{ idCheckMessage }}</span>
                      </p>

                      <p
                        v-else
                        :style="{
                          fontSize: '0.8rem',
                          marginTop: '-10px',
                          marginLeft: '4px',
                        }"
                      >
                        <span>{{ idCheckMessage }}</span>
                      </p>

                      <!--  -->
                      <div class="mb-3">
                        <material-input
                          id="user-pw"
                          type="password"
                          label=" "
                          size="lg"
                          v-model="userPw"
                        />
                      </div>

                      <!--   -->
                      <div class="mb-3">
                        <material-input
                          id="user-pw-check"
                          type="password"
                          label="  "
                          size="lg"
                          v-model="pwCheck"
                        />
                      </div>
                      <p
                        v-if="pwChecked"
                        :style="{
                          color: pwChecked == 'true' ? '#2ecc71' : '#e74c3c',
                          fontSize: '0.8rem',
                          marginTop: '-10px',
                          marginLeft: '4px',
                        }"
                      >
                        {{ pwCheckedMessage }}
                      </p>

                      <p
                        v-else
                        :style="{
                          fontSize: '0.8rem',
                          marginTop: '-10px',
                          marginLeft: '4px',
                        }"
                      >
                        {{ pwCheckedMessage }}
                      </p>

                      <!--   -->
                      <material-checkbox
                        id="flexCheckDefault"
                        class="font-weight-light"
                        v-model="agree"
                      >
                         
                        <!-- <a
                          href="../../../pages/privacy.html"
                          class="text-dark font-weight-bolder"
                        >
                          |  </a
                        > -->
                      </material-checkbox>
                      <p
                        v-if="!agreeCheck"
                        :style="{
                          color: '#e74c3c',
                          fontSize: '0.8rem',
                          marginTop: '-10px',
                          marginLeft: '4px',
                        }"
                      >
                        {{ agreeCheckMessage }}
                      </p>

                      <!--    -->
                      <div class="d-flex justify-content-between gap-3 mt-4">
                        <!--   -->
                        <material-button
                          variant="gradient"
                          color="success"
                          size="lg"
                          class="w-45"
                          type="button"
                          @click="goToStep('user')"
                          > </material-button
                        >

                        <!--   -->
                        <material-button
                          variant="gradient"
                          color="success"
                          size="lg"
                          class="w-45"
                          type="button"
                          @click="goToStep('org')"
                          > </material-button
                        >
                      </div>
                    </form>

                    <!--   -->
                    <sign-up-user-form
                      v-else-if="step === 'user'"
                      :base="{ userId, userPw, agree }"
                      @submit="signUpSubmit('user', $event)"
                      @back="step = 1"
                    />

                    <!--   -->
                    <sign-up-org-form
                      v-else-if="step === 'org'"
                      :base="{ userId, userPw, agree }"
                      @submit="signUpSubmit('org', $event)"
                      @back="step = 1"
                    />
                  </div>
                  <div class="px-1 pt-0 text-center card-footer px-lg-2">
                    <p class="mx-auto mb-4 text-sm">
                       ? |
                      <router-link
                        :to="{ name: 'SignIn' }"
                        class="text-success text-gradient font-weight-bold"
                        ></router-link
                      >
                    </p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </main>
  </div>

<!-- ======================= -->
<!--    -->
<!-- ======================= -->
<div
  v-if="showTerms"
  class="terms-modal-backdrop"
  @click.self="showTerms = false"
>
  <div class="terms-modal">
    <h4 class="mb-3"></h4>

    <div class="terms-content">
      <h5>   </h5>
      <p>
           ( )    
        ( )  ,     
        .
      </p>

      <h6>1 ()</h6>
      <p>
                 
             .
      </p>

      <h6>2 ()</h6>
      <p>
        1.         
        .<br />
        2.       .<br />
        3.         .
      </p>

      <h6>3 ( )</h6>
      <p>
           .<br />
        1.       <br />
        2.    <br />
        3.     <br />
        4.      <br />
      </p>

      <h6>4 ( )</h6>
      <p>
               
            .   
        .
      </p>

      <h6>5 ( )</h6>
      <p>
        1.         .<br />
        2.        .<br />
        3.     ,    .
      </p>

      <h6>6 ( )</h6>
      <p>
              ,    
        .
      </p>

      <hr />
<!-- 
      <p class="mt-3 text-sm text-muted">
                    .
      </p> -->
    </div>

    <div class="text-end mt-3">
      <material-button color="success" variant="gradient" @click="showTerms = false">
        
      </material-button>
    </div>
  </div>
</div>



</template>

<script>
import Navbar from '@/examples/PageLayout/Navbar.vue';
import MaterialInput from '@/components/MaterialInput.vue';
import MaterialCheckbox from '@/components/MaterialCheckbox.vue';
import MaterialButton from '@/components/MaterialButton.vue';
const body = document.getElementsByTagName('body')[0];
import { mapMutations } from 'vuex';
import SignUpUserForm from '@/components/SignUp/SignUpUserForm.vue';
import SignUpOrgForm from '@/components/SignUp/SignUpOrgForm.vue';
import { checkId as checkUserId, addUser, addOrg } from '../api/user';
import dateFormat from '../utils/dateFormat';

const today = dateFormat(new Date(), 'yyyy-MM-dd');

export default {
  name: 'sign-up',
  components: {
    Navbar,
    MaterialInput,
    MaterialCheckbox,
    MaterialButton,
    SignUpUserForm,
    SignUpOrgForm,
  },
  data() {
    return {
      // step: 1,
      // userId: 'test12',
      // userPw: '123',
      // pwCheck: '123',
      // agree: true,
      // idCheck: 'true',
      // idCheckMessage: '   .',
      // pwChecked: 'true',
      // pwCheckedMessage: ' .',
      // agreeCheck: true,
      // agreeCheckMessage: '',
      step: 1,
      userId: '',
      userPw: '',
      pwCheck: '',
      agree: false,
      idCheck: null,
      idCheckMessage: '   .',
      pwChecked: null,
      pwCheckedMessage: ' .',
      agreeCheck: true,
      agreeCheckMessage: '',
      showTerms: false,
    };
  },
  watch: {
    userId() {
      this.idCheck = null;
      this.idCheckMessage = '   .';
    },
    pwCheck() {
      if (this.pwCheck == '') {
        this.pwChecked = null;
        this.pwCheckedMessage = ' .';
      } else if (this.userPw != this.pwCheck) {
        this.pwChecked = 'false';
        this.pwCheckedMessage = ' .';
      } else {
        this.pwChecked = 'true';
        this.pwCheckedMessage = ' .';
      }
    },
  },
  beforeMount() {
    this.toggleEveryDisplay();
    this.toggleHideConfig();
    body.classList.remove('bg-gray-100');
  },
  beforeUnmount() {
    this.toggleEveryDisplay();
    this.toggleHideConfig();
    body.classList.add('bg-gray-100');
  },
  methods: {
    ...mapMutations(['toggleEveryDisplay', 'toggleHideConfig']),

    // 
    async checkId() {
      if (!this.userId) {
        this.idCheck = 'false';
        this.idCheckMessage = ' .';
        return;
      }
      try {
        const result = await checkUserId(this.userId);
        if (result.ok) {
          this.idCheck = 'true';
          this.idCheckMessage = '  .';
        } else {
          this.idCheck = 'false';
          this.idCheckMessage = '   .';
        }
      } catch (err) {
        alert('  .');
      }
    },

    goToStep(type) {
      //  
      if (this.idCheck != 'true') {
        this.idCheck = 'false';
        this.idCheckMessage = ' .';
        return;
      }
      if (this.pwChecked != 'true') {
        this.pwChecked = 'false';
        this.pwCheckedMessage = ' .';
        return;
      }
      if (!this.agree) {
        this.agreeCheck = false;
        this.agreeCheckMessage = '   .';
        return;
      }
      this.step = type; // user / org
    },

    async signUpSubmit(type, detail) {
      //  
      if (type == 'user') {
        try {
          const payload = {
            userId: this.userId,
            userPw: this.userPw,
            agree: this.agree,
            joinDate: today,
            ...detail,
          };
          const result = await addUser(payload);

          if (!result.ok) {
            alert(' .\n .');
            return;
          }
          this.$router.push({ name: 'SignIn' });
          alert(' .\n  .');
        } catch (err) {
          alert(' .\n .');
        }
      }
      //  
      if (type == 'org') {
        try {
          const payload = {
            userId: this.userId,
            userPw: this.userPw,
            agree: this.agree,
            joinDate: today,
            ...detail,
          };

          const result = await addOrg(payload);

          if (!result.ok) {
            alert(' .\n .');
            return;
          }
          this.$router.push({ name: 'SignIn' });
          alert(' .\n  .');
        } catch (err) {
          alert(' .\n .');
        }
      }
    },
  },
};
</script>

<style scoped>
.check-btn {
  padding: 10px;
  width: 80px;
  height: 45px;
}

/*   */
.terms-modal-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.45);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2000;
}

/*   */
.terms-modal {
  background: #fff;
  width: 600px;
  max-width: 90%;
  max-height: 80%;
  overflow-y: auto;
  padding: 25px;
  border-radius: 10px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.25);
}

/*   */
.terms-content {
  max-height: 60vh;
  overflow-y: auto;
}

</style>
