<template>
  <div class="container py-4">
    <div class="mb-3">
      <h3 class="fw-bold text-start text-dark"></h3>
    </div>

    <!--  -->
    <div class="tabs-container">
      <div>
        <material-button
          :class="['tab-btn', activeTab === 'user' ? 'active' : 'inactive']"
          @click="
            (activeTab = 'user'), (editMode = false), (orgEditMode = false)
          "
        >
           
        </material-button>
      </div>

      <div>
        <material-button
          :class="['tab-btn', activeTab === 'org' ? 'active' : 'inactive']"
          @click="
            (activeTab = 'org'), (editMode = false), (orgEditMode = false)
          "
        >
           
        </material-button>
      </div>

      <div v-if="auth.role === 'AA1'">
        <material-button
          :class="['tab-btn', activeTab === 'child' ? 'active' : 'inactive']"
          @click="activeTab = 'child'"
        >
           
        </material-button>
      </div>
    </div>

    <div class="info-wrapper">
      <material-card class="info-card">
        <material-card-content class="info-content">
          <!--   -->
          <div v-if="activeTab === 'user'">
            <UserBasicInfo
              :editMode="editMode"
              :userData="userData"
              @edit="editMode = true"
              @cancel="cancelUserEdit"
              @save="saveUserInfo"
              @openPwModal="showPwModal = true"
            />
          </div>

          <!--   -->
          <div v-if="activeTab === 'org'">
            <UserOrgInfo
              :role="auth.role"
              :editMode="orgEditMode"
              :orgData="orgData"
              @edit="orgEditMode = true"
              @cancel="cancelOrgEdit"
              @save="saveOrgInfo"
            />
          </div>

          <!--   -->
          <div v-if="activeTab === 'child'">
            <UserChildInfo
              :childList="childList"
              @add-child="addChild"
              @update-child="updateChild"
              @delete-child="deleteChild"
            />
          </div>

          <UserPasswordModal
            v-if="showPwModal"
            @close="showPwModal = false"
            @confirm="submitPasswordChange"
          />
        </material-card-content>
      </material-card>
    </div>
  </div>
</template>

<script>
import { useAuthStore } from '@/store/authLogin';
import UserBasicInfo from '@/components/UserInfo/UserBasicInfo.vue';
import UserOrgInfo from '@/components/UserInfo/UserOrgInfo.vue';
import UserChildInfo from '@/components/UserInfo/UserChildInfo.vue';
import UserPasswordModal from '@/components/UserInfo/UserPasswordModal.vue';
import {
  findUserInfo,
  addChild,
  deleteChild,
  updateInfo,
  updatePw,
} from '@/api/user';
import dateFormat from '@/utils/dateFormat';

export default {
  name: 'UserInfo',

  components: {
    UserBasicInfo,
    UserOrgInfo,
    UserChildInfo,
    UserPasswordModal,
  },
  data() {
    return {
      activeTab: 'user',
      editMode: false,
      orgEditMode: false,
      showPwModal: false,
      userData: {
        user_id: '',
        name: '',
        phone: '',
        address: '',
        email: '',
      },
      orgData: {
        orgCode: '',
        orgName: '',
        managerName: '',
        deptName: '',
        roleName: '',
        phone: '',
        address: '',
      },
      childList: [],
      showChildForm: false,
      originalUser: {},
      originalOrg: {},
      auth: null,
    };
  },

  created() {
    const auth = useAuthStore();
    this.auth = auth;

    this.userData.user_id = auth.userId;
    this.userData.name = '';
    this.userData.phone = '';
    this.userData.address = '';
    this.userData.email = '';
    this.originalUser = { ...this.userData };

    this.orgData.orgCode = '';
    this.orgData.orgName = '';
    this.orgData.managerName = '';
    this.orgData.deptName = '';
    this.orgData.roleName = auth.role;
    this.orgData.phone = '';
    this.orgData.address = '';
    this.originalOrg = { ...this.orgData };

    this.userFullInfo();

    this.childList = [];
  },

  methods: {
    cancelUserEdit() {
      this.userData = { ...this.originalUser };
      this.editMode = false;
    },

    cancelOrgEdit() {
      this.orgData = { ...this.originalOrg };
      this.orgEditMode = false;
    },

    async saveUserInfo(updated) {
      const result = await updateInfo('user', updated);
      if (!result.ok) {
        alert(result.message);
        return;
      }
      await this.userFullInfo();
      this.editMode = false;
      alert(' .');
    },

    async submitPasswordChange(passwords) {
      const data = JSON.parse(localStorage.getItem('user'));
      const payload = {
        user_code: data.user_code,
        newPw: passwords.newPw,
      };

      const result = await updatePw(payload);
      console.log(result);
      if (!result.ok) {
        alert(' .');
        return;
      }

      alert(' .');
      this.showPwModal = false;
    },

    async saveOrgInfo(updated) {
      const updateData = {
        user_id: this.auth.userId,
        org_name: updated.orgName,
        department: updated.deptName,
      };

      const result = await updateInfo('org', updateData);
      if (!result.ok) {
        alert(result.message);
        return;
      }

      // 1) DB    
      await this.userFullInfo();

      // 2)  (authStore) orgCode 
      if (this.orgData.orgCode) {
        this.auth.updateOrgCode(this.orgData.orgCode);
      } else {
        console.warn('[saveOrgInfo] orgData.orgCode .', this.orgData);
      }

      // 3)   
      this.orgEditMode = false;
      alert(' ');
    },

    async updateChild(childData) {
      const result = await updateInfo('child', this.auth.role, childData);
      if (!result.ok) {
        alert(result.message);
        return;
      }
      await this.userFullInfo();
    },

    //  
    async addChild(child) {
      try {
        const today = dateFormat(new Date(), 'yyyy-MM-dd');
        const childData = {
          user_code: this.auth.userCode,
          registered_date: today,
          ...child,
        };

        const result = await addChild(childData);
        if (result.ok) {
          await this.userFullInfo();
        }
      } catch (err) {
        console.error('[ addChild  ]', err);
      }
    },

    //   
    async deleteChild(childCode) {
      const result = await deleteChild(childCode);
      if (!result.ok) {
        alert(' .');
        return;
      }
      await this.userFullInfo();
      alert(' .');
    },

    async userFullInfo() {
      try {
        const res = await findUserInfo({
          userId: this.auth.userId,
          role: this.auth.role,
        });

        this.auth.userName = res[0].name;
        if (res.false) {
          console.log(' ');
          return;
        }

        this.userData = {
          user_id: res[0].user_id,
          name: res[0].name,
          phone: res[0].phone,
          address: res[0].user_address,
          email: res[0].email,
        };
        this.originalUser = { ...this.userData };

        this.orgData = {
          orgCode: res[0].org_code,
          orgName: res[0].org_name || ' ',
          managerName: res[0].manager_name || ' ',
          deptName: res[0].department,
          roleName: this.auth.role,
          phone: res[0].org_phone,
          address: res[0].org_address,
        };
        this.originalOrg = { ...this.orgData };

        const childData = res;
        this.childList = childData
          .filter((item) => item.child_name != null)
          .map((item) => ({
            child_code: item.child_code,
            child_name: item.child_name,
            ssn: item.ssn,
            gender: item.gender,
            disability_type: item.disability_type,
          }));
      } catch (err) {
        console.error('[ loadUserFullInfo  ]', err);
      }
    },
  },
};
</script>

<style>
material-button,
.material-button {
  cursor: pointer !important;
}

.tabs-container {
  display: flex;
  gap: 8px;
  margin-bottom: 20px;
}

.tab-btn {
  padding: 6px 16px !important;
  border-radius: 8px !important;
  font-size: 13px;
  font-weight: 600;
}

.tab-btn.active {
  background: #5e72e4 !important;
  color: white !important;
}

.tab-btn.inactive {
  background: #e9ecef !important;
  color: #344767 !important;
}

.info-wrapper {
  display: flex;
  justify-content: center;
}

.info-card {
  width: 460px;
  border-radius: 12px;
}

.info-content {
  padding: 24px !important;
}

.material-button,
button,
button:hover,
.btn,
.btn:hover {
  cursor: pointer !important;
}
</style>
