<template>
  <div class="pw-modal-overlay">
    <div class="pw-modal-box">
      <h5 class="pw-title"> </h5>

      <!--   -->
      <div class="pw-input-block">
        <label class="pw-label"> </label>
        <MaterialInput
          v-model="passwords.current"
          type="password"
          class="w-100"
        />
      </div>

      <!--   -->
      <div class="pw-input-block">
        <label class="pw-label"> </label>
        <MaterialInput
          v-model="passwords.newPw"
          type="password"
          class="w-100"
        />
      </div>

      <!--    -->
      <div class="pw-input-block">
        <label class="pw-label">  </label>
        <MaterialInput
          v-model="passwords.newPwCheck"
          type="password"
          class="w-100"
        />
      </div>

      <div class="pw-btn-area">
        <material-button color="secondary" @click="$emit('close')">
          
        </material-button>
        <material-button color="success" @click="confirmClick">
          
        </material-button>
      </div>
    </div>
  </div>
</template>

<script>
import MaterialInput from '@/components/MaterialInput.vue';

export default {
  name: 'UserPasswordModal',
  components: { MaterialInput },
  data() {
    return {
      passwords: {
        current: '',
        newPw: '',
        newPwCheck: '',
      },
    };
  },
  methods: {
    confirmClick() {
      if (
        !this.passwords.current ||
        !this.passwords.newPw ||
        !this.passwords.newPwCheck
      ) {
        alert('  ');
        return;
      }
      if (this.passwords.newPw != this.passwords.newPwCheck) {
        alert(' ');
        return;
      }
      this.$emit('confirm', this.passwords);
    },
  },
};
</script>

<style scoped>
/*   */
.pw-modal-overlay {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.38);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2500;
}

/*  */
.pw-modal-box {
  width: 360px;
  padding: 24px;
  background: #fff;
  border-radius: 14px;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.18);
}

/*  */
.pw-title {
  font-size: 18px;
  font-weight: 700;
  margin-bottom: 18px;
  color: #344767;
}

/*   */
.pw-input-block {
  margin-bottom: 18px;
}

/*  */
.pw-label {
  font-size: 12px;
  font-weight: 700;
  letter-spacing: 0.03em;
  text-transform: uppercase;
  color: #8392ab;
  display: block;
  margin-bottom: 6px;
}

/*   */
.pw-btn-area {
  display: flex;
  justify-content: flex-end;
  gap: 10px;
  margin-top: 4px;
}
</style>
