<template>
  <div>
    <!--  -->
    <div class="d-flex gap-2">
      <material-input
        id="phone"
        type="text"
        label=" ( -   )"
        v-model="localPhone"
        :disabled="localIsVerified"
        size="lg"
        class="mb-3"
        @input="validatePhone"
      />
      <!-- <material-button
        color="success"
        class="sms-btn"
        :disabled="localIsVerified"
        @click="sendCode"
      >
         
      </material-button> -->
    </div>
    <!--   -->
    <p
      v-if="sendMessage"
      :style="{
        color: sendMessage == '  ' ? '#2ecc71' : '#e74c3c',
        fontSize: '0.8rem',
        marginTop: '-10px',
        marginLeft: '4px',
      }"
    >
      {{ sendMessage }}
    </p>

    <!--  -->
    <!-- <div class="d-flex gap-2 mb-3">
      <material-input
        label=" 6"
        v-model="smsCode"
        :disabled="localIsVerified"
        size="lg"
      />
      <material-button
        color="success"
        class="sms-btn"
        :disabled="localIsVerified"
        @click="verifyCode"
      >
         
      </material-button>
    </div> -->

    <!--   -->
    <p
      v-if="message"
      :style="{
        color: localIsVerified == true ? '#2ecc71' : '#e74c3c',
        fontSize: '0.8rem',
        marginTop: '-10px',
        marginLeft: '4px',
      }"
    >
      {{ message }}
    </p>
  </div>
</template>

<script>
import MaterialInput from '@/components/MaterialInput.vue';
// import MaterialButton from '@/components/MaterialButton.vue';
import { sendSMS, verifySMS } from '../../api/user';

export default {
  name: 'SmsVerification',
  components: {
    MaterialInput,
    // MaterialButton,
  },
  props: {
    phone: String,
    isVerified: Boolean,
  },

  emits: ['update:phone', 'update:isVerified'],

  data() {
    return {
      localPhone: '',
      localIsVerified: false,
      smsCode: '',
      sendMessage: '',
      message: '',
    };
  },

  watch: {
    localPhone(v) {
      this.$emit('update:phone', v);
    },
    localIsVerified(v) {
      this.$emit('update:isVerified', v);
    },
  },

  methods: {
    validatePhone() {
      //  
      if (this.localPhone.includes('-')) {
        alert('-  .');
        this.localPhone = this.localPhone.replace(/-/g, '');
        return;
      }

      // 
      this.localPhone = this.localPhone.replace(/[^0-9]/g, '');

      // 11 
      if (this.localPhone.length > 11) {
        this.localPhone = this.localPhone.slice(0, 11);
      }
    },
    async sendCode() {
      if (!this.localPhone) {
        return;
      }
      const result = await sendSMS(this.localPhone.replace(/-/g, ''));
      this.sendMessage = result.ok ? '  ' : result.message;
    },

    async verifyCode() {
      if (!this.smsCode) {
        return;
      }
      const result = await verifySMS(
        this.localPhone.replace(/-/g, ''),
        this.smsCode
      );
      if (!result.ok) {
        this.message = result.message;
        return;
      }
      this.localIsVerified = true;
      this.message = '  ';
    },
  },
};
</script>

<style scoped>
.sms-btn {
  height: 44px !important;
  display: inline-flex !important;
  align-items: center !important;
  justify-content: center !important;
  white-space: nowrap !important;
  padding: 0 18px !important;
  width: auto !important;
  line-height: 1 !important;
}
</style>
