// id, pw 
export async function findIdPw(type, findData) {
  try {
    if (!type == 'findResetPw') {
      const result = await axios.post('/api/user/findIdPw', {
        type,
        data: findData,
      });
      return result.data;
    }
    const result = await axios.put('/api/user/findIdPw', {
      type,
      data: findData,
    });
    return result.data;
  } catch (err) {
    console.log('[ user.js ID/PW   ]', err);
  }
}

