// src/store/authLogin.js

import { defineStore } from 'pinia';
import { login as loginApi } from '../api/user';

export const useAuthStore = defineStore('authLogin', {
  state: () => ({
    userId: '',
    userName: '',
    role: 'AA0',
    userCode: '',
    orgCode: '',
    isLogin: false,
  }),
  getters: {
    isAA1: (state) => state.role == 'AA1',
    isAA2: (state) => state.role == 'AA2',
    isAA3: (state) => state.role == 'AA3',
  },
  actions: {
    reload() {
      //   
      //    reload 
      // localStorage   loginCheck -> true
      // loginCheck JSON   -> loginData
      // loginData  pinia   
      //     
      // ->   
      const loginCheck = localStorage.getItem('user');
      if (!loginCheck) {
        this.userId = '';
        this.userName = '';
        this.userCode = '';
        this.role = 'AA0';
        this.isLogin = false;
        return;
      }

      try {
        const loginData = JSON.parse(loginCheck);
        this.userId = loginData.user_id;
        this.userName = loginData.name || '';
        this.role = loginData.role;
        this.userCode = loginData.userCode || loginData.user_code || "";
        this.orgCode = loginData.org_code;
        this.isLogin = true;
      } catch (err) {
        console.error('[ pinia reload  ] : ', err);
        //  
        this.userId = '';
        this.userName = '';
        this.userCode = '';
        this.role = 'AA0';
        this.isLogin = false;
      }
    },

    // 
    async login({ userId, userPw }) {
      const result = await loginApi({ userId, userPw });
      // is_active 
      if (result.is_active == 0) {
        return { ok: false, err: '   ' };
      }
      // ,    false  SignIn.vue  
      if (!result.ok) {
        throw new Error(result.message);
      }

      this.userId = result.user_id;
      this.userName = result.name || '';
      this.role = result.role;
      this.userCode = result.user_code;
      this.orgCode = result.org_code;
      this.isLogin = true;

      localStorage.setItem(
        'user',
        JSON.stringify({
          userId: result.user_id,
          userName: result.name || '',
          role: result.role,
          userCode: result.user_code,
          orgCode: result.org_code,
        })
      );
      return result;
    },

    // 
    logout() {
      this.userId = '';
      this.userName = '';
      this.role = 'AA0';
      this.userCode = '';
      this.orgCode = '';
      this.isLogin = false;
      localStorage.removeItem('user');
    },

    updateOrgCode(newOrgCode) {
      this.orgCode = newOrgCode;

      const saved = localStorage.getItem('user');
      if (saved) {
        try {
          const obj = JSON.parse(saved);
          obj.orgCode = newOrgCode;
          obj.org_code = newOrgCode;
          localStorage.setItem('user', JSON.stringify(obj));
        } catch (e) {
          console.error('[updateOrgCode localStorage ]', e);
        }
      }
    },
  },
});
