<template>
  <navbar btnBackground="bg-gradient-success" />
  <div
    class="page-header align-items-start min-vh-100"
    style="
      background-image: url('https://images.unsplash.com/photo-1497294815431-9365093b7331?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1950&q=80');
    "
  >
    <span class="mask bg-gradient-dark opacity-6"></span>
    <div class="container my-auto">
      <div class="row">
        <div class="col-lg-4 col-md-8 col-12 mx-auto">
          <div class="card z-index-0 fadeIn3 fadeInBottom">
            <!--   -->
            <div class="card-header p-0 position-relative mt-n4 mx-3 z-index-2">
              <div
                class="bg-gradient-success shadow-success border-radius-lg py-3 pe-1"
              >
                <h4 class="text-white font-weight-bolder text-center mt-2 mb-0">
                     
                </h4>
              </div>
            </div>

            <!--   -->
            <div class="card-body">
              <!-- form -->
              <form role="form" class="text-start mt-3">
                <div class="mb-3">
                  <material-input
                    id="userId"
                    type="text"
                    label=" "
                    name="userId"
                    v-model="userId"
                  />
                </div>
                <div class="mb-3">
                  <material-input
                    id="userPw"
                    type="password"
                    label=" "
                    name="userPw"
                    v-model="userPw"
                  />
                </div>
                <!-- <material-switch id="rememberMe" name="rememberMe"
                  >Remember me</material-switch
                > -->
                <div class="text-center">
                  <material-button
                    class="my-4 mb-2"
                    variant="gradient"
                    color="success"
                    fullWidth
                    type="button"
                    @click="login"
                    ></material-button
                  >
                </div>
                <p class="mt-4 text-sm text-center">
                   ?
                  <router-link
                    :to="{ name: 'SignUp' }"
                    class="text-success text-gradient font-weight-bold"
                  >
                    </router-link
                  >
                </p>

                <p class="mt-4 text-sm text-center">
                  <a
                    href="#"
                    @click.prevent="toggleIdModal"
                    class="text-success text-gradient font-weight-bold"
                  >
                    ID 
                  </a>
                  <span> / </span>
                  <a
                    href="#"
                    @click.prevent="togglePwModal"
                    class="text-success text-gradient font-weight-bold"
                  >
                    PW 
                  </a>
                </p>
              </form>
            </div>
          </div>
        </div>
      </div>

      <!-- ID   -->
      <div v-if="showFindIdModal" class="custom-modal-backdrop">
        <div class="custom-modal">
          <h5 class="font-weight-bolder mb-3"> </h5>

          <p class="text-sm text-secondary">   .</p>

          <material-input
            type="text"
            label=""
            class="mb-3"
            v-model="findIdName"
          />

          <material-input
            type="tel"
            label=""
            class="mb-4"
            v-model="findIdPhone"
            @input="formatPhoneInput('findIdPhone')"
          />

          <div class="d-flex justify-content-end gap-2">
            <material-button color="secondary" @click="toggleIdModal">
              
            </material-button>
            <material-button color="success" @click="findId">
              
            </material-button>
          </div>
        </div>
      </div>

      <!-- PW   -->
      <div v-if="showFindPwModal" class="custom-modal-backdrop">
        <div class="custom-modal">
          <div v-if="findPwStep === 1">
            <h5 class="font-weight-bolder mb-3">  (1/2)</h5>

            <p class="text-sm text-secondary">
                 .
            </p>

            <material-input
              type="text"
              label=""
              class="mb-3"
              v-model="findPwUserId"
            />
            <material-input
              type="text"
              label=""
              class="mb-3"
              v-model="findPwName"
            />
            <material-input
              type="tel"
              label=""
              class="mb-4"
              v-model="findPwPhone"
              @input="formatPhoneInput('findPwPhone')"
            />

            <div class="d-flex justify-content-end gap-2">
              <material-button color="secondary" @click="togglePwModal">
                
              </material-button>
              <material-button color="success" @click="findPw">
                 
              </material-button>
            </div>
          </div>

          <div v-else-if="findPwStep === 2">
            <h5 class="font-weight-bolder mb-3">  (2/2)</h5>

            <p class="text-sm text-secondary">
                 .
            </p>

            <material-input
              type="password"
              label=" "
              class="mb-3"
              v-model="findPwNewPw"
            />
            <material-input
              type="password"
              label="  "
              class="mb-4"
              v-model="findPwNewPwCheck"
            />

            <div class="d-flex justify-content-end gap-2">
              <material-button color="secondary" @click="togglePwModal">
                
              </material-button>
              <material-button color="success" @click="resetPassword">
                 
              </material-button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Navbar from '@/examples/PageLayout/Navbar.vue';
import MaterialInput from '@/components/MaterialInput.vue';
import MaterialButton from '@/components/MaterialButton.vue';
import { mapMutations } from 'vuex';
import { useAuthStore } from '../store/authLogin';
import { useMenuStore } from '@/store/sidebar';
import { findIdPw } from '../api/user';

export default {
  name: 'sign-in',

  components: {
    Navbar,
    MaterialInput,
    // MaterialSwitch,
    MaterialButton,
  },
  data() {
    return {
      userId: '',
      userPw: '',
      showFindIdModal: false,
      showFindPwModal: false,
      findIdName: '',
      findIdPhone: '',
      findPwUserId: '',
      findPwName: '',
      findPwPhone: '',
      findPwNewPw: '',
      findPwNewPwCheck: '',
      findPwStep: 1, // 1:  , 2: 
    };
  },
  beforeMount() {
    this.toggleEveryDisplay();
    this.toggleHideConfig();
  },
  beforeUnmount() {
    this.toggleEveryDisplay();
    this.toggleHideConfig();
  },
  methods: {
    async login() {
      if (!this.userId || !this.userPw) {
        alert('   ');
        return;
      }

      const piniaLogin = useAuthStore();
      const menu = useMenuStore();
      // menu.loadUserRole();
      try {
        const result = await piniaLogin.login({
          userId: this.userId,
          userPw: this.userPw,
        });
        if (!result.ok) {
          alert(` : ${result.err}`);
          return;
        }

        alert(` ${result.user_id}(${result.role})`);
        this.$router.push({ name: 'dashboard' });
        localStorage.setItem('user', JSON.stringify(result));
        menu.setRole(result.role);
      } catch (err) {
        alert(' : ', err);
      }
    },

    formatPhoneInput(targetVariable) {
      let phone = this[targetVariable];
      if (!phone) return;

      const cleaned = phone.replace(/[^0-9]/g, '');
      let formatted = '';

      if (cleaned.length < 4) {
        formatted = cleaned;
      } else if (cleaned.length < 8) {
        formatted = cleaned.slice(0, 3) + '-' + cleaned.slice(3);
      } else if (cleaned.length < 11) {
        formatted =
          cleaned.slice(0, 3) +
          '-' +
          cleaned.slice(3, 7) +
          '-' +
          cleaned.slice(7);
      } else {
        formatted =
          cleaned.slice(0, 3) +
          '-' +
          cleaned.slice(3, 7) +
          '-' +
          cleaned.slice(7, 11);
      }

      this[targetVariable] = formatted;
    },

    async findId() {
      if (!this.findIdName || !this.findIdPhone) {
        alert('  ');
        return;
      }
      const result = await findIdPw('findId', {
        name: this.findIdName,
        phone: this.findIdPhone,
      });
      if (!result.ok) {
        alert('   ');
        return;
      }
      alert(`  ${result.user_id} .`);
    },

    async findPw() {
      if (!this.findPwUserId || !this.findPwName || !this.findPwPhone) {
        return alert('  ');
      }
      const result = await findIdPw('findPw', {
        user_id: this.findPwUserId,
        name: this.findPwName,
        phone: this.findPwPhone,
      });
      if (!result.ok) {
        alert('   ');
        return;
      }
      this.findPwStep = 2;
    },

    async resetPassword() {
      if (!this.findPwNewPw || !this.findPwNewPwCheck) {
        return alert(' ');
      }
      if (this.findPwNewPw !== this.findPwNewPwCheck) {
        return alert(' ');
      }
      const result = await findIdPw('findResetPw', {
        user_id: this.findPwUserId,
        newPw: this.findPwNewPw,
      });
      if (!result.ok) {
        alert('PW  ');
        return;
      }
      alert('  ');
      this.togglePwModal();
      this.findPwStep = 1;
    },

    toggleIdModal() {
      this.findResetModal();
      this.showFindIdModal = !this.showFindIdModal;
    },
    togglePwModal() {
      this.findResetModal();
      this.showFindPwModal = !this.showFindPwModal;
    },

    findResetModal() {
      this.findIdName = '';
      this.findIdPhone = '';
      this.findPwUserId = '';
      this.findPwName = '';
      this.findPwPhone = '';
      this.findPwNewPw = '';
      this.findPwNewPwCheck = '';
      this.findPwStep = 1;
    },

    ...mapMutations(['toggleEveryDisplay', 'toggleHideConfig']),
  },
};
</script>

<style>
.custom-modal-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1050;
}

.custom-modal {
  background: white;
  padding: 30px;
  border-radius: 12px;
  width: 90%;
  max-width: 400px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
}
</style>
